/* LOGO */

.logo {
  position: absolute;
  top: 15px;
  left: 10px;
  padding: 0.5em;
  color: var(--theme-primary);
  text-decoration: none;

  &__header {
    padding: 0;
    margin: 0;
    font-family: var(--logo-font);
  }

  &__caption {
    padding: 0;
    margin: 0;
  }
}
